import React, {Component} from "react";
import SummaryTable from "./SummaryTable";
import {SummaryTableHeader} from "./SummaryTableHeader";
import SummaryDetails from "./SummaryDetails";
import SummaryDetailsForBundles from "./SummaryDetailsForBundles";
import {HideWhenCartIsEmpty} from "./Utils";
import {OraLoader} from "eshop/components/OraCommonComponents";

export default class MulticartSummaryComponent extends Component {

    componentDidMount() {
        this.props.fetchCart();
        this.props.fetchMiniCart();
    }

    getProps() {
        return {
            monthlyHeader: this.props.descriptions.monthlyHeader,
            oneTimeHeader: this.props.descriptions.oneTimeHeader,
            checkoutPriceTooltipContent: this.props.descriptions.checkoutPriceTooltipContent || "płatność przy odbiorze przesyłki",
            firstMonthPriceTooltipContent: this.props.descriptions.firstMonthPriceTooltipContent || "płatności uwzględnione w pierwszej fakturze",
            entryType: this.props.entryType,
            parentCmp: this.props.component +"-summaryTableHeader",
        };
    }
    render() {
        if(!this.props.hasCartData) return null;
        return (
            <HideWhenCartIsEmpty className="l-full-row l-page-row g-white1-bg u-small-padding-top-l u-padding-l-xl">
                <table role="presentation"
                       className="u-no-margin-t opl-table opl-conf-summary-table opl-table--50-50 u-no-border">
                    <SummaryTableHeader title={this.props.descriptions.title} {...this.getProps()}/>
                    <OraLoader loading={this.props.updating} parentId='summary-loader'>
                        <SummaryTableAndDetails {...this.props} getProps={this.getProps()}/>
                    </OraLoader>
                </table>
            </HideWhenCartIsEmpty>
        );
    }
}


class SummaryTableAndDetails extends Component {
    render() {
        return (
            <div id="SummaryTableAndDetails">
                <SummaryTable
                    title={this.props.descriptions.title}
                    id={this.props.component + "-table"}
                    renderHeader={false}
                    {...this.props.getProps}/>
                {this.props.showNewSummaryDetails ? (
                    <SummaryDetailsForBundles
                        rootComponent={this.props.component}
                        id={this.props.component + "-details"}
                        title="Szczegóły opłat"
                        {...this.props.getProps}>
                    </SummaryDetailsForBundles>
                ) : (
                    <SummaryDetails
                        rootComponent={this.props.component}
                        id={this.props.component + "-details"}
                        title="Szczegóły opłat"
                        {...this.props.getProps}>
                    </SummaryDetails>
                )}
            </div>
        );
    }
}

